export function bg(value, base = 0) {
  if (base === 0) {
    return BigNumber(value);
  } else if (base > 0) {
    return BigNumber(value).times(BigNumber("1" + "0".repeat(base)));
  } else {
    return BigNumber(value).div(BigNumber("1" + "0".repeat(-base)));
  }
}
export function random(min, max) {
  return Math.round(Math.random() * Math.max(0, max - min));
}

export function getProviderRpc(chainId) {
  const providers = RPC_PROVIDERS[chainId];
  return providers[random(0, providers.length - 1)];
}

export function getProvider(chainId) {
  chainId = Number(chainId)
  const rpc = getProviderRpc(chainId);
  const network = SUPPORTED_MAINNET_NETWORKS.find(
    (network) => network.chainId === chainId
  );
  if (!network) {
    throw new Error(`Invalid network ${chainId}`);
  }
  return new ethers.providers.StaticJsonRpcProvider(rpc, {
    chainId,
    name: network.name,
  });
}

export function bigNumberify(n) {
  try {
    return eBigNumber.from(n)
  } catch (e) {
    console.error('bigNumberify error', e)
    return undefined
  }
}

export async function getGasLimit(
  contract,
  method,
  params,
  value
) {
  const defaultValue = bigNumberify(0);
  if (!value) {
    value = defaultValue;
  }
  let gasLimit;
  try {
    gasLimit = await contract.estimateGas[method](...params, { value });
    if (gasLimit.lt(22000)) {
      gasLimit = bigNumberify(22000);
    }
    return gasLimit.mul(11000).div(10000); // add a 10% buffer  
  } catch (error) {
    return gasLimit = bigNumberify(MAX_GAS_LIMIT)
    // throw error;
  }

}

export function deriNatural(value) {
  return bg(value, -18);
}

