export function convertToInternationalCurrencySystem(labelValue, decimalScale) {

  // Nine Zeroes for Billions
  return Number.isNaN(labelValue) ? ''
    : Math.abs(Number(labelValue)) >= 1.0e+9

      ? (Math.abs(Number(labelValue)) / 1.0e+9).toFixed(decimalScale) + "B"
      // Six Zeroes for Millions 
      : Math.abs(Number(labelValue)) >= 1.0e+6

        ? (Math.abs(Number(labelValue)) / 1.0e+6).toFixed(decimalScale) + "M"
        // Three Zeroes for Thousands
        : Math.abs(Number(labelValue)) >= 1.0e+3

          ? (Math.abs(Number(labelValue)) / 1.0e+3).toFixed(decimalScale) + "K"

          : Math.abs(Number(labelValue).toFixed(decimalScale));

}

export function getAmountETHAndDeri(current_tick, tick_lower, tick_upper, liquidity) {
  let total_amount0 = 0;
  let total_amount1 = 0;
  let amount0 = 0;
  let amount1 = 0;
  let current_sqrt_price = tick_to_price(current_tick / 2)
  let sa = tick_to_price(tick_lower / 2)
  let sb = tick_to_price(tick_upper / 2)
  if (tick_upper <= current_tick) {
    let amount1 = liquidity * (sb - sa)
    total_amount1 += amount1
  } else if (tick_lower < current_tick && current_tick < tick_upper) {
    let amount0 = liquidity * (sb - current_sqrt_price) / (current_sqrt_price * sb)
    amount1 = liquidity * (current_sqrt_price - sa)

    total_amount0 += amount0
    total_amount1 += amount1
  } else {
    amount0 = liquidity * (sb - sa) / (sa * sb)
    total_amount0 += amount0
  }
  return [total_amount0, total_amount1]
}

export function tick_to_price(tick) {
  const TICK_BASE = 1.0001
  return TICK_BASE ** tick
}

