export function isMobile() {
  return /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
}

export function isIE() {
  return /*@cc_on!@*/false || !!document.documentMode;
}

export function isEdge() {
  return !isIE && !!window.StyleMedia;
}

export function storeSkipConfirmation(skipConfirmation) {
  if (skipConfirmation) {
    sessionStorage.setItem(SKIP_TRADE_CONFIRMATION, '1')
  } else {
    sessionStorage.setItem(SKIP_TRADE_CONFIRMATION, '0')
  }
}

export function isElementInViewport(el, offset = 0) {
  var rect = el ? el.getBoundingClientRect() : {}
  return (
    rect.top >= 0 &&
    rect.left >= 0 &&
    rect.bottom + offset <= (window.innerHeight || document.documentElement.clientHeight) && /* or $(window).height() */
    rect.right <= (window.innerWidth || document.documentElement.clientWidth) /* or $(window).width() */
  );
}

export function isScrollBottom(offset = 0) {
  return (window.innerHeight + window.scrollY) >= document.body.offsetHeight - offset
}

export function isScrollRight(el, offset = 0) {
  return el.clientWidth + el.scrollLeft >= el.scrollWidth - offset
}

export function isScrollLeft(el, offset = 0) {
  return el.clientWidth + el.scrollLeft >= el.scrollWidth - offset
}

export function dataFormater(number) {
  if (number > 1000000000) {
    return (number / 1000000000).toString() + 'B';
  } else if (number > 1000000) {
    return (number / 1000000).toString() + 'M';
  } else if (number > 1000) {
    return (number / 1000).toString() + 'K';
  } else {
    return number.toString();
  }
}

export function isSingleDomain() {
  return !process.env.NODE_ENV || process.env.NODE_ENV === 'development' || process.env.REACT_APP_SINGLE_DOMAIN === 'on'
}

export function camelize(str) {
  return str.replace(/(?:^\w|[A-Z]|\b\w)/g, function (word, index) {
    return index === 0 ? word.toUpperCase() : word.toLowerCase();
  }).replace(/\s+/g, '');
}

export function formatNumber(number, decimal) {
  let effectiveDecimal = decimal
  let value = number;
  if (/\d+\.0*[1-9]+/.test(value) && (+bg(value).toFixed((decimal || 2))) === 0) {
    effectiveDecimal = countDecimal(Math.abs(value)) + 2
  }
  value = bg(value).toFixed(effectiveDecimal)
}
export function setCookie(name, value, expires = 365, domain = COOKIE_DERI_DOMAIN, path = '/') {
  if (name && value) {
    Cookies.set(name, value, { expires: expires, domain: domain, path: path })
  }
}

export function getCookie(name) {
  return Cookies.get(name)
}
export function removeCookie(name, domain = COOKIE_DERI_DOMAIN, path = '/') {
  Cookies.remove(name, { domain, path })
}

export function numberWithCommas(x) {
  if (x) {
    var parts = x.toString().split(".");
    return parts[0].replace(/\B(?=(\d{3})+(?=$))/g, ",") + (parts[1] ? "." + parts[1] : "")
  }
  return x;
}

export function buried(event, parameter, value) {
  if (window.gtag) {
    let obj = {
      'app_name': 'deri.io',
    }
    obj[parameter] = value
    window.gtag('event', event, obj);
  }
}

export function calculateOpenInterest(
  volume,
  trading,
) {
  let openVolume = trading.contract.openVolume
  let bool = false;
  volume = Math.abs(volume);
  volume = bg(openVolume).plus(volume).toNumber();
  let liquidity = bg(trading.contract.liquidity).plus(trading.contract.lpsPnl)
  if (trading.currentSymbol.isFuture && openVolume) {
    let left = bg(trading.contract.initialMarginRatioOrigin).div(trading.contract.alpha);
    let right = bg(trading.tradeInfo.indexPrice)
      .times(volume)
      .div(liquidity);
    bool = left.lt(right);
  } else if (trading.currentSymbol.isOption && openVolume) {
    let openInterestRatio = bg(trading.tradeInfo.indexPrice)
      .times(volume)
      .div(liquidity);
    let leftOne = bg(trading.contract.minInitialMarginRatio);
    let leftTwo = bg(trading.contract.initialMarginRatioOrigin);
    let rightOne = bg(Math.abs(trading.contract.delta))
      .times(trading.contract.alpha).div(1)
      .times(openInterestRatio);
    let rightTwo = bg(trading.contract.alpha).times(openInterestRatio);
    bool = leftOne.lt(rightOne) && leftTwo.lt(rightTwo);
  } else if (trading.currentSymbol.isPower && openVolume) {
    let openInterestRatio = bg(trading.contract.theoreticalPrice)
      .times(volume)
      .div(liquidity);
    let left = bg(trading.contract.initialMarginRatioOrigin);
    let right = bg(2).times(trading.contract.alpha).times(openInterestRatio);
    bool = left.lt(right);
  }
  return bool;
}