import ApiProxy from "./ApiProxy";


export default class History {

   async load(wallet, symbolInfo, isOptions) {
      if (wallet && wallet.isSupportChain(isOptions) && wallet.detail.chainId && symbolInfo && symbolInfo.address) {
         const symbolParam = symbolInfo.isAllV3 ? symbolInfo.symbol : symbolInfo.symbolId
         let all = await ApiProxy.request('getTradeHistory', [wallet.detail.chainId, symbolInfo.address, wallet.detail.account, symbolParam]);
         if (all && all.length > 0) {
            all = all.map((item) => {
               let symbol = item.displaySymbol.toLowerCase()
               if (symbol.indexOf('gamma') > 0) {
                  item.price = "--"
                  item.notional = "--"
               }
               return item
            })
         }
         return all;
      } else {
         return []
      }
   }
}