import { observable, action, computed, makeObservable, autorun } from "mobx";
import Position from "./Position";
import Contract from "./Contract";
import History from "./History";
import {
  eqInNumber,
  storeConfig,
  getConfigFromStore,
  getDefaultNw,
  percentTimesAmount,
  precision,
  firstLetterUppercase,
  toPlainString,
  getCookie,
  setCookie,
  equalIgnoreCase,
  buried,
  calculateOpenInterest,
} from "../utils/utils";
import { priceCache, DeriEnv } from "../lib/web3js/index";
import { bg } from "../lib/web3js/index";
import Intl from "./Intl";
import version from "./Version";
import type from "./Type";
import Type from "./Type";
import PoolManager from "./PoolManager";
import ApiProxy from "./ApiProxy";
// import {numberWithCommas} from '../utils/utils'
import {
  OPTION,
  FUTURE,
  BUY,
  TRADE_ENTRY_VOLUME_STATUS,
  TRADE_ENTRY_DOLLAR_STATUS,
  TRADE_ENTRY_PERCENTAGE_STATUS,
  TRADE_ENTRY_SLIDER_STATUS,
  SELL,
  TRADING_APPEND_POSITION_STATUS,
  TRADING_CLOSE_POSITION_STATUS,
  TRADING_CREATE_POSITION_STATUS,
  SKIP_TRADE_CONFIRMATION,
  LONG,
  SHORT,
  CALL_OPTION,
  VERSION_V3,
  VERSION_V3_LITE,
  VERSION_V2_LITE,
  VERSION_V2,
  POWER,
  SHORT_NAME_MAP,
  COOKIE_DEFAULT_POWERS_SYMBOL_KEY,
  COOKIE_DEFAULT_FUTURES_SYMBOL_KEY,
  COOKIE_DEFAULT_OPTIONS_SYMBOL_KEY,
  FUTURES,
} from "../utils/Constants";
import Category from "./Category";
import WebSocket from "./WebSocket";

const GET_TRADE_IFNO_URL = `${process.env.REACT_APP_REST_SERVER_URL}/pool_symbol_state`;

/**
 * 交易模型
 * 关联对象
 * 1. chain
 * 2. Oracle
 * 3. position
 * 4. contract
 * 5. history
 * 计算
 * 1. dynamic balance
 * 2. available blance
 * 响应事件
 * 1. chain change
 * 2. chain’s symbol changed
 * 3. index update
 * 4. volum change
 * 5. margin change
 * 输出
 * 1. dynamic balance
 * 2. margin
 * 3. available balance
 * 4. volume
 * 5. specs
 * 6. spec
 * 7. position
 * 8. contract
 * 9. fundingRate
 */

export default class Trading {
  version = null;
  wallet = null;
  type = null;
  volume = "";
  openInterst = false;
  paused = false;
  slideIncrementMargin = 0;
  position = {};
  positions = [];
  contract = {};
  fundingRate = {};
  volatility = "";
  history = null;
  userSelectedDirection = "long";
  supportChain = true;
  optionsConfigs = {};
  pools = [];
  allPools = [];
  pool = {};
  closeOnly = false;
  inputPercentageValue = "";
  direction = "";
  entryStatus = TRADE_ENTRY_VOLUME_STATUS;
  inputDollarValue = "";
  inputSliderValue = "";
  impactPreview = {};
  symbols = [];
  symbolsWithMarkprice = [];
  currentSymbol = {};
  beforeChangedSymbol = {};
  loaded = false;
  venusEarned = "";
  slippage = 0.005;
  webSocket = new WebSocket(process.env.REACT_APP_BACKEND_WSS_URL, {
    onConnectError: () => this.loadTradeDataFromRest(this.pool, this.symbolInfo),
    onReconnect: () => this.loadTradeDataFromWebSocket(this.pool, this.symbolInfo),
    onDisconnect: () => (this.subscribed = false),
  });
  tradeInfo = {};
  subscribed = false;
  gammaEntryPrice = "";

  constructor() {
    makeObservable(this, {
      volume: observable,
      openInterst: observable,
      inputPercentageValue: observable,
      slideIncrementMargin: observable,
      fundingRate: observable,
      volatility: observable,
      position: observable,
      positions: observable,
      history: observable,
      contract: observable,
      userSelectedDirection: observable,
      supportChain: observable,
      pools: observable,
      allPools: observable,
      pool: observable,
      symbols: observable,
      symbolsWithMarkprice: observable,
      currentSymbol: observable,
      closeOnly: observable,
      direction: observable,
      entryStatus: observable,
      inputDollarValue: observable,
      inputSliderValue: observable,
      impactPreview: observable,
      venusEarned: observable,
      loaded: observable,
      slippage: observable,
      tradeInfo: observable,
      gammaEntryPrice: observable,
      setVenusEarned: action,
      setOpenInterst: action,
      setWallet: action,
      setSymbols: action,
      setCurrentSymbol: action,
      setCloseOnly: action,
      setContract: action,
      setPosition: action,
      setPositions: action,
      setVolume: action,
      setUserSelectedDirection: action,
      setFundingRate: action,
      setVolatility: action,
      setHistory: action,
      setDirection: action,
      setEntryStatus: action,
      setInputDollarValue: action,
      setInputSliderValue: action,
      setImpactPreview: action,
      setSymbolsWithMarkprice: action,
      setSlippage: action,
      setPaused: action,
      setPool: action,
      setAllPools: action,
      setTradeInfo: action,
      setLoaded: action,
      setGammaEntryPrice: action,
      setInputPercentageValue: action,
      TransactionFeeTip: computed,
      fundingFeeValueTip: computed,
      fundingCoefficientTip: computed,
      rateTip: computed,
      multiplierTip: computed,
      powerMultiplierTip: computed,
      powerMarkPriceTip: computed,
      isNegative: computed,
      isPositive: computed,
      feeDisplay: computed,
      maxEntryVolume: computed,
      minEntryVolume: computed,
      curTradeUnit: computed,
      minTradeScaleDecimal: computed,
      calcMinTradeScaleDecimal: computed,
      dollarValue: computed,
      maxDollarValue: computed,
      maxAvailableMargin: computed,
      positionTradingStatus: computed,
      sliderValue: computed,
      volumeValue: computed,
      displayVolume: computed,
      realTradeVolume: computed,
      hasFormatUnit: computed,
      hasSpecialFormat: computed,
      formatSliderValue: computed,
      sliderStyle: computed,
      accountInfo: computed,
      inputHelpText: computed,
      directionDisplay: computed,
      tradeWarn: computed,
      initialMarginRatio: computed,
      maintenanceMarginRatio: computed,
      isAccountReady: computed,
      isTradingReady: computed,
      skipTradeConfirmation: computed,
      fundingFeeTip: computed,
      symbolInfo: computed,
      symbolInfos: computed,
      directionChanged: computed,
      volumeChanged: computed,
      tradeBToken: computed,
      isRetired: computed,
      takePrice: computed, // 开仓价格,
      displaySymbolMultiplier: computed,
      index: computed,
      markPrice: computed,
      gammaEntryPricePlacehold: computed,
    });
    this.positionInfo = new Position();
    this.contractInfo = new Contract();
    this.historyInfo = new History();
    this.poolManager = new PoolManager();
    autorun(async () => {
      this.loadTransSummary();
    });
  }

  async loadTransSummary() {
    if (this.realTradeVolume && this.wallet && this.symbolInfo && this.accountInfo) {
      const { chainId, account } = this.wallet.detail;
      const { symbolId, address } = this.symbolInfo;
      const symbolParam = this.symbolInfo.isAllV3 ? this.symbolInfo.symbol : symbolId;
      let params = [chainId, address, Math.abs(this.realTradeVolume), symbolParam];
      const fee = await ApiProxy.request("getEstimatedFee", params);
      const volume = this.direction === SELL ? -this.realTradeVolume : this.realTradeVolume;
      params = [chainId, address, account, volume, symbolParam];
      const liqPrice = await ApiProxy.request("getEstimatedLiquidatePrice", params);
      params = [chainId, address, volume, symbolParam];
      const markPriceAfter = await ApiProxy.request("getEstimatedTimePrice", params);

      const liqPriceBefore =
        this.positionTradingStatus === TRADING_CREATE_POSITION_STATUS
          ? this.position.liquidationPrice
          : this.position.liquidationPrice &&
          (this.position.liquidationPrice.price1 || this.position.liquidationPrice.price2 || "0");

      const marginUsageAfter =
        this.positionTradingStatus === TRADING_CLOSE_POSITION_STATUS
          ? bg(this.accountInfo.marginUsage)
            .minus(this.accountInfo.curSymbolMarginUsage)
            .plus(bg(this.accountInfo.curSymbolMarginUsage).minus(this.sliderValue).abs())
            .toNumber()
          : bg(this.accountInfo.marginUsage).plus(this.sliderValue).abs().toNumber();
      const unrealizedPnl = this.calculateUnrealizePnl(volume);
      const availableMarginAfter = bg(this.accountInfo.dynEffMargin)
        .plus(unrealizedPnl)
        .minus(fee)
        .minus(marginUsageAfter)
        .toNumber();
      const impact = {
        fee: fee,
        tradePrice: markPriceAfter,
        availableMargin: this.accountInfo.availableMargin,
        availableMarginAfter: availableMarginAfter < 0 ? 0 : availableMarginAfter,
        marginUsage: this.accountInfo.marginUsage,
        marginUsageAfter: marginUsageAfter,
        liqPrice: liqPriceBefore,
        liqPriceAfter: liqPrice,
      };
      this.setImpactPreview(impact);
    }
  }

  async loadSymbolsWithprice() {
    const chainId =
      this.wallet && this.wallet.detail && this.wallet.supportChain
        ? this.wallet.detail.id
        : getDefaultNw(DeriEnv.get()).id;
    if (this.symbols && this.symbols.length > 0) {
      const params = [chainId, this.allPools.map((pool) => pool.address)];
      let symbols = await ApiProxy.request("getPoolMarkPrices24hInfo", params);
      this.setSymbolsWithMarkprice(symbols);
    }
  }

  getChainId(wallet) {
    if (wallet.isConnected()) {
      if (wallet.supportCurNetwork) {
        return wallet.detail.id;
      } else {
        return getDefaultNw(DeriEnv.get()).id;
      }
    } else if (wallet.status === "disconnected" || wallet.status === "error") {
      return getDefaultNw(DeriEnv.get()).id;
    }
  }

  /**
   * 初始化
   * wallet and version changed will init
   */
  async init(wallet, finishedCallback, isRetired, symbol, baseToken = "") {
    const chainId = this.getChainId(wallet);
    if (!chainId) {
      return;
    }
    const isOption = Type.isOption;
    const type = Type.current;
    let pools = await this.poolManager.loadByType(type);
    let allPools = await this.poolManager.loadAllPool();
    if (isRetired) {
      pools = pools.filter((p) => p.retired || p.symbols.some((s) => s.isCloseOnly));
      allPools = allPools.filter((p) => p.retired || p.symbols.some((s) => s.isCloseOnly));
    } else {
      pools = pools.filter((p) => !p.retired);
      allPools = allPools.filter((p) => !p.retired);
    }

    pools = pools.filter(
      (p) =>
        eqInNumber(chainId, p.chainId) && p.symbols.some((s) => s.category === Category.current)
    );
    allPools = allPools.filter((p) => eqInNumber(chainId, p.chainId));
    if (!pools || pools.length === 0 || !allPools || allPools.length === 0) {
      console.error("can not pool in current network");
      return;
    }
    this.setWallet(wallet);
    this.setAllPools(allPools);
    this.setPools(pools);
    const allSymbols = isRetired
      ? allPools.reduce((total, pool) => total.concat(pool.symbols), []).filter((s) => s.retired)
      : allPools
        .reduce((total, pool) => total.concat(pool.symbols), [])
        .filter((s) => !s.offline && !s.isCloseOnly);
    let pool = null;
    let defaultSymbol = null;
    const cookie = this.getSymbolFromCookie();
    //symbol in path 优先级最高，cookie 次之，默认第一个池子和池子的第一个symbol
    if (symbol) {
      //&& p.bTokens.some(t => equalIgnoreCase(t.bTokenSymbol, baseToken))
      pool = pools.find(
        (p) =>
          equalIgnoreCase(p.bTokenSymbol, baseToken) &&
          p.symbols.some((s) => equalIgnoreCase(s.displaySymbol, symbol))
      );
      defaultSymbol = pool && pool.symbols.find((s) => equalIgnoreCase(s.displaySymbol, symbol));
    } else if (cookie && cookie.symbol && !isRetired) {
      pool = pools.find(
        (p) =>
          (equalIgnoreCase(p.bTokenSymbol, cookie.baseToken) ||
            p.bTokens.some((t) => equalIgnoreCase(t.bTokenSymbol, cookie.baseToken))) &&
          p.symbols.some((s) => equalIgnoreCase(s.displaySymbol, cookie.symbol))
      );
      defaultSymbol = allSymbols.find(
        (s) =>
          equalIgnoreCase(s.symbol, cookie.symbol) &&
          equalIgnoreCase(s.bTokenSymbol, cookie.baseToken)
      );
    }
    if (!pool) {
      pool = pools[0];
    }
    if (!pool) {
      console.error("can not find pool with symbol ", symbol);
      return;
    }
    //url 和 cookie 都没有 指明symbol，从当前pool的
    if (!defaultSymbol && pool) {
      defaultSymbol = Type.isOption
        ? pool.symbols.find((s) => s.symbol === "ETHUSD-2000-C") || pool.symbols[0]
        : pool.symbols.find((s) => s.category === Category.current);
    }

    this.setPool(pool);
    this.setSymbols(allSymbols);
    this.setCurrentSymbol(defaultSymbol);
    if (defaultSymbol.isAllV3) {
      //如果切换symbol，取消订阅
      if (!this.subscribed && this.pool.address && this.beforeChangedSymbol.symbol) {
        this.unSubscribe();
      }
      this.loadTradeDataFromWebSocket(pool, defaultSymbol);
    } else {
      this.loadTradeDataFromRest(pool, defaultSymbol);
    }
    this.loadBySymboInfo(this.wallet, defaultSymbol, finishedCallback, isOption);
  }

  async loadTradeDataFromRest(pool, symbolInfo) {
    if (pool.chainId && pool.address && symbolInfo.symbol) {
      const url = `${GET_TRADE_IFNO_URL}/${pool.chainId}/${pool.address}/${symbolInfo.symbol}`;
      const res = await fetch(url).catch((e) => console.error(e));
      if (res && res.ok) {
        const result = await res.json();
        if (result.success && result.data.symbol === symbolInfo.symbol) {
          this.setTradeInfo(result.data);
        }
      }
    }
  }

  loadTradeDataFromWebSocket(pool = this.pool, symbolInfo = this.currentSymbol) {
    if (!this.subscribed && pool.chainId && pool.address && symbolInfo.symbol) {
      const args = [pool.chainId, pool.address, symbolInfo.symbol];
      const channel = `symbol_state.${args.join(".")}`;
      this.webSocket.subscribe(
        "add_channel",
        {
          api: "symbol_state",
          args: args,
        },
        (data) => {
          if (this.pool.chainId === data.chainId && this.symbolInfo.symbol === data.symbol) {
            this.setTradeInfo(data);
          }
        },
        channel,
        false
      );
      this.subscribed = true;
    }
  }

  unSubscribe() {
    this.webSocket.unsubscribe("remove_channel", {
      api: "symbol_state",
      args: [
        this.beforeChangedSymbol.chainId,
        this.beforeChangedSymbol.address,
        this.beforeChangedSymbol.symbol,
      ],
    });
  }

  async loadBySymboInfo(wallet, currentSymbol, finishedCallback, isOption) {
    if (currentSymbol) {
      this.pause();
      this.loadSymbolsWithprice();
      const positions = await this.positionInfo.loadAll(wallet, currentSymbol, (positions) => {
        this.setLoaded(true);
        !this.paused && this.setPositions(positions);
      });
      this.setPositions(positions);
      this.contractInfo.load(wallet, currentSymbol, (contract) => this.setContract(contract));
      const histories = await this.historyInfo.load(wallet, currentSymbol, isOption);
      this.setHistory(histories);
      this.getVenusEarned(wallet, currentSymbol);
      this.resume();
      finishedCallback && finishedCallback();
    } else {
      finishedCallback && finishedCallback();
    }
  }

  refreshCache() {
    const currentSymbol = this.currentSymbol;
    const symbol = type.isOption ? currentSymbol.symbol.split("-")[0] : currentSymbol.symbol;
    priceCache.clear();
    priceCache.update(currentSymbol, symbol);
  }

  //优先使用session storage 的，如果缓存跟用户当前链一直，则命中缓存，否则取当前配置第一条
  getDefaultConfig(configs = [], wallet) {
    let defaultConfig = null;
    if (configs.length > 0) {
      const fromStore = this.getFromStore();
      if (fromStore && eqInNumber(wallet.detail.chainId, fromStore.chainId)) {
        defaultConfig = fromStore;
      }
      if (defaultConfig) {
        //虽然从缓存获得config ，需要判断池子地址是否一致，否则用可用config的第一条
        const pos = configs.findIndex((c) => c.pool === defaultConfig.pool);
        if (pos === -1) {
          defaultConfig = configs[0];
        }
      } else {
        defaultConfig = configs[0];
      }
    }
    return defaultConfig;
  }

  //存起来
  store(config) {
    storeConfig(version.current, config);
  }

  getFromStore() {
    return getConfigFromStore(version.current);
  }

  async getVenusEarned(wallet, symbolInfo) {
    const chainId =
      wallet && wallet.isConnected() ? wallet.detail.chainId : getDefaultNw(DeriEnv.get()).id;
    if (symbolInfo && symbolInfo.supportVenus && symbolInfo.isAllV3 && wallet) {
      const begin = new Date().getTime();
      const venusEarned = await ApiProxy.request("getVenusEarned", [
        chainId,
        symbolInfo.address,
        wallet.detail.account,
        { asTrader: true },
      ]);
      console.log("getVenusEarned take time ", (new Date().getTime() - begin) / 1000);
      this.setVenusEarned(venusEarned);
    }
  }

  reset() {
    this.setInputDollarValue("");
    this.setInputSliderValue("");
    this.setInputPercentageValue("");
    this.setVolume("");
  }

  async refresh() {
    this.reset();
    this.setCloseOnly(false);
    // const position = await this.positionInfo.load(this.wallet, this.symbolInfo)
    // this.setPosition(position);
    const positions = await this.positionInfo.loadAll(this.wallet, this.symbolInfo);
    this.setPositions(positions);
    const history = await this.historyInfo.load(this.wallet, this.currentSymbol);
    if (history) {
      this.setHistory(history);
    }
  }

  /**
   * 暂停实时读取index和定时读取position
   */
  pause() {
    this.setPaused(true);
    this.positionInfo.pause();
  }

  /**
   * 恢复读取
   */
  resume() {
    this.setPaused(false);
    this.positionInfo.resume();
  }

  setCookie(value, category, baseToken = "") {
    setCookie(this.getCookieKey(category), JSON.stringify({ symbol: value, baseToken: baseToken }));
  }

  getSymbolFromCookie() {
    const cookie = getCookie(this.getCookieKey(Category.current));
    if (cookie) {
      try {
        const obj = JSON.parse(cookie);
        return obj;
      } catch (e) {
        return null;
      }
    }
    return null;
  }

  getCookieKey(category) {
    if (category === FUTURES) {
      return COOKIE_DEFAULT_FUTURES_SYMBOL_KEY;
    } else if (category === OPTION) {
      return COOKIE_DEFAULT_OPTIONS_SYMBOL_KEY;
    } else {
      return COOKIE_DEFAULT_POWERS_SYMBOL_KEY;
    }
  }

  setLoaded(loaded) {
    this.loaded = loaded;
  }

  setWallet(wallet) {
    this.wallet = wallet;
  }

  setPools(pools) {
    this.pools = pools;
  }

  setPool(pool) {
    this.pool = pool;
  }

  setAllPools(allPools) {
    this.allPools = allPools;
  }

  setTradeInfo(tradeInfo) {
    this.tradeInfo = tradeInfo;
  }

  setSymbols(symbols) {
    this.symbols = symbols;
  }

  setCurrentSymbol(symbol) {
    this.currentSymbol = symbol;
  }

  setSymbolsWithMarkprice(symbols) {
    this.symbolsWithMarkprice = symbols;
  }

  setVenusEarned(venusEarned) {
    this.venusEarned = venusEarned;
  }

  setPosition(position) {
    if (position) {
      this.position = position;
    }
  }

  setPositions(positions) {
    if (positions) {
      this.positions = positions.filter((p) => +p.volume !== 0);
      this.setPosition(positions.find((p) => p.symbol === this.currentSymbol.symbol));
    }
  }

  setContract(contract) {
    this.contract = contract;
  }

  setHistory(history) {
    this.history = history;
  }

  setFundingRate(fundingRate) {
    this.fundingRate = fundingRate;
  }

  setVolatility(volatility) {
    this.volatility = volatility;
  }

  setSlippage(value) {
    if (!isNaN(value)) {
      this.slippage = value;
    }
  }

  setVolume(volume) {
    this.volume = volume;
  }

  setInputPercentageValue(percentage) {
    this.inputPercentageValue = percentage;
    if (percentage) {
      const curNotional = percentTimesAmount(percentage, this.maxAvailableMargin);
      const volume = this.calculateVolume(curNotional);
      this.setVolume(volume);
    }
  }

  setInputDollarValue(dollarValue) {
    this.inputDollarValue = dollarValue;
    const volume = dollarValue
      ? this.calculateVolume(bg(dollarValue).times(this.initialMarginRatio))
      : "";
    this.setVolume(volume);
  }

  setInputSliderValue(value) {
    this.setEntryStatus(TRADE_ENTRY_SLIDER_STATUS);
    this.inputSliderValue = value;
    const volume = this.calculateVolume(value);
    this.setVolume(volume);
  }

  calculateVolume(value) {
    if (this.position && this.contract && this.symbolInfo && this.initialMarginRatio) {
      const volume = value ? (value / this.takePrice / this.initialMarginRatio).toFixed(this.calcMinTradeScaleDecimal) : 0;
      return volume;
    }
    return 0;
  }

  setPaused(paused) {
    this.paused = paused;
  }

  setOpenInterst(bool) {
    this.openInterst = bool;
  }

  setUserSelectedDirection(direction) {
    this.userSelectedDirection = direction;
  }

  setCloseOnly(closeOnly) {
    this.closeOnly = closeOnly;
  }

  setDirection(direction) {
    this.direction = direction;
  }

  setEntryStatus(status) {
    this.entryStatus = status;
  }

  setImpactPreview(impact) {
    this.impactPreview = impact;
  }

  groupConfigBySymbol(symbols = []) {
    return symbols.reduce((total, config) => {
      const symbol = config.symbol.split("-")[0];
      if (!total[symbol]) {
        total[symbol] = [];
      }
      config["underlier"] = symbol;
      config["zone"] = symbol;
      total[symbol].push(config);
      return total;
    }, []);
  }

  //资金费率
  async loadFundingRate(wallet, currentSymbol, isOption) {
    if (currentSymbol) {
      const chainId =
        wallet && wallet.isConnected() && wallet.isSupportChain(isOption)
          ? wallet.detail.chainId
          : getDefaultNw(DeriEnv.get()).id;
      if (currentSymbol) {
        const symbolParam = currentSymbol.isAllV3 ? currentSymbol.symbol : currentSymbol.symbolId;
        const res = await ApiProxy.request("getFundingRate", [
          chainId,
          currentSymbol.address,
          symbolParam,
        ]).catch((e) => console.error("getFundingRate was error,maybe network is wrong"));
        return res;
      }
    }
  }

  async doTransaction(onAccept) {
    this.pause();
    const volume = toPlainString(
      this.direction === SELL ? -this.realTradeVolume : this.realTradeVolume
    );
    const symbolParam = this.pool.isAllV3 ? this.symbolInfo.symbol : this.symbolInfo.symbolId;
    const displayVolume = this.hasFormatUnit
      ? Math.abs(this.realTradeVolume)
      : Math.abs(this.displayVolume);
    const notional = bg(displayVolume)
      .times(this.symbolInfo.isOption ? this.tradeInfo.indexPrice : this.tradeInfo.markPrice)
      .toNumber()
      .toFixed(2);
    const api = this.pool.deriBased ? "tradeWithMargin" : "requestExecute";
    const params = this.pool.deriBased
      ? [
        this.wallet.detail.chainId,
        this.currentSymbol.address,
        this.wallet.detail.account,
        volume,
        symbolParam,
        this.slippage,
      ]
      : [
        this.wallet.detail.chainId,
        this.currentSymbol.address,
        this.wallet.detail.account,
        this.pool.routerAddress,
        volume,
        symbolParam,
        this.gammaEntryPrice || -1,
        this.slippage,
      ];

    const res = await ApiProxy.request(
      api,
      params,
      {
        includeResponse: true,
        write: true,
        subject: `${Intl.get("trade", this.direction)} ${Intl.get("trade", "order")}`,
        title: {
          text:
            this.direction === BUY
              ? Intl.get("trade", "place-long-order")
              : Intl.get("trade", "place-short-order"),
          icon: this.symbolInfo.isFuture ? this.symbolInfo.symbol : this.symbolInfo.underlier,
        },
        content: {
          success: `${displayVolume} ${this.symbolInfo.unit} ${Category.isGamma ? "" : `$(${notional})`} `,
          error: Intl.get("trade", "transaction-failed"),
        },
        onAccept: onAccept,
        isExecutor: this.pool.deriBased ? false : true,
        symbol: this.symbolInfo.symbol
      },
      true
    );
    if (res && res.success) {
      buried("open_complete", "trade_status", "open_position_complete");
      this.refresh();
    }
    this.resume();
  }

  clean() {
    this.pause();
    this.positionInfo.clean();
    this.contractInfo.clean();
    this.version = null;
    if (this.currentSymbol && this.currentSymbol.symbol) {
      this.beforeChangedSymbol = this.currentSymbol;
    }
    this.setCurrentSymbol({});
    this.setTradeInfo({});
    this.setVolume("");
    this.setVolatility("");
    this.setPosition({});
    this.setPositions([]);
    this.setContract({});
    this.setHistory(null);
    this.setLoaded(false);
    this.subscribed = false;
    // this.webSocket.disconnect()
  }

  setGammaEntryPrice(value) {
    this.gammaEntryPrice = value;
  }

  get index() {
    return this.tradeInfo && this.tradeInfo.indexPrice;
  }

  get markPrice() {
    return this.tradeInfo && this.tradeInfo.markPrice;
  }

  get accountInfo() {
    const position = this.position;
    if (
      position &&
      position.margin !== undefined &&
      position.unrealizedPnlTotal !== undefined &&
      position.marginHeldTotal !== undefined
    ) {
      const {
        volume = 0,
        margin = 0,
        unrealizedPnlTotal = 0,
        fundingFeeTotal = 0,
        marginHeldTotal = 0,
        price = 0,
        marginHeldBySymbol = 0,
      } = position;
      const dynEffMargin = bg(margin).plus(unrealizedPnlTotal).minus(fundingFeeTotal);
      const marginUsagePercent = dynEffMargin.gt(0)
        ? bg(marginHeldTotal).div(dynEffMargin).times(100)
        : bg(0);
      return {
        dynEffMargin: dynEffMargin.toString(),
        marginUsage: marginHeldTotal,
        availableMargin: dynEffMargin.minus(marginHeldTotal).lt(0)
          ? 0
          : dynEffMargin.minus(marginHeldTotal).toString(),
        curSymbolMarginUsage: marginHeldBySymbol,
        curSymbolNotional: bg(volume)
          .abs()
          .times(this.initialMarginRatio)
          .times(this.takePrice)
          .toString(),
        marginUsagePercent: marginUsagePercent.gt(200) ? 200 : marginUsagePercent.toFixed(2),
      };
    }
    return {};
  }

  //正仓
  get isPositive() {
    return bg(this.position.volume).gt(0);
  }

  //负仓
  get isNegative() {
    return bg(this.position.volume).isNegative();
  }

  calculateTransactionFee(volume) {
    const fee = this.feeDisplay;
    const cost = this.calculateCost(volume);
    if (this.symbolInfo.isOption) {
      if (this.contract.isCall) {
        if (this.contract.strike >= this.position.price) {
          return bg(cost).times(fee[2]).div(100).abs().toNumber();
        } else {
          return bg(this.position.price).times(volume).times(fee[2]).div(100).abs().toNumber();
        }
      } else {
        if (this.contract.strike < this.position.price) {
          return bg(cost).times(fee[2]).div(100).abs().toNumber();
        } else {
          return bg(this.position.price).times(volume).times(fee[2]).div(100).abs().toNumber();
        }
      }
    } else {
      return bg(cost).times(this.contract.feeRatio).abs().toNumber();
    }
  }

  calculateCost(volume) {
    if (this.wallet && this.pool) {
      const params = [this.pool.chainId, this.pool.address, volume, this.currentSymbol.symbol];
      const cost = ApiProxy.syncRequest("getEstimatedDpmmCost", params);
      return cost;
    }
    return 0;
  }

  calculateUnrealizePnl(volume) {
    const price = this.symbolInfo.isFuture ? this.position.price : this.position.theoreticalPrice
    const unrealizedPnl = this.symbolInfo.isGamma
      ? ApiProxy.syncRequest('calculateGammaOpenUnrealizedPnl', [this.pool.chainId, this.pool.address, volume])
      :
      bg(price).times(volume).minus(this.calculateCost(volume)).toNumber();
    return unrealizedPnl;
  }

  get displaySymbolMultiplier() {
    return 1 / SHORT_NAME_MAP[this.symbolInfo.formattedUnit];
  }

  get feeDisplay() {
    if (type.isFuture) {
      return this.contract.feeRatio ? this.contract.feeRatio * 100 : 0;
    } else {
      const isLess = bg(this.index)
        .times(this.contract.feeRatioNotional)
        .lt(bg(this.markPrice).times(this.contract.feeRatioMark));
      if (isLess) {
        return this.contract.underlier
          ? [
            `${this.contract.underlier} ${Intl.get("trade", "price")}`,
            "*",
            this.contract.feeRatioNotional * 100,
          ]
          : ["", ""];
      } else {
        return [Intl.get("trade", "eo-mark-price"), "*", this.contract.feeRatioMark * 100];
      }
    }
  }

  get fundingFeeValueTip() {
    if (this.tradeInfo && this.tradeInfo.fundingPerSecond && this.symbolInfo) {
      if (Category.isGamma) {
        let text = { token: this.symbolInfo.bTokenSymbol }
        return `${Intl.eval("trade", "daily-funding-value", text)}`
      }
      const text = this.symbolInfo.isPower
        ? { unit: this.symbolInfo.displaySymbol }
        : { unit: this.symbolInfo.unit };
      return `${Intl.eval("trade", "1-long-contract-pays-1-short-contract", text)} :
            <br> ${this.tradeInfo.funding0} ${this.symbolInfo.bTokenSymbol} /  ${Intl.get(
        "trade",
        "day"
      )}  ${Intl.get("trade", "or")} 
            <br> ${this.tradeInfo.fundingPerSecond} ${this.symbolInfo.bTokenSymbol} /  ${Intl.get(
        "trade",
        "second"
      )}`;
    }
    return "";
  }

  get rateTip() {
    if (this.tradeInfo && this.tradeInfo.funding0 && this.tradeInfo.markPrice) {
      if (Category.isGamma) {
        let text = { symbol: this.symbolInfo.powerUnderlier }
        return `${Intl.eval("trade", "daily-funding-rate", text)}`
      }
      return `${Intl.get("trade", "rate-hover-one")} ${bg(this.tradeInfo.funding0)
        .div(bg(this.tradeInfo.markPrice))
        .times(bg(100))
        .toString()}% * ${Intl.get("trade", "rate-hover-two")}`;
    }
    return "";
  }

  get powerMultiplierTip() {
    if (
      this.symbolInfo &&
      this.symbolInfo.displaySymbol &&
      this.symbolInfo.symbol &&
      this.contract.displayMultiplier
    ) {
      let unitName = {
        "BTCUSD-Gamma": "BTC-Gamma",
        "ETHUSD-Gamma": "ETH-Gamma",
      }
      let unit = Category.isGamma ? unitName[this.symbolInfo.symbol] : this.symbolInfo.symbol
      return Intl.eval("trade", "power-multiplier-hover", {
        unit: unit,
        symbol: this.symbolInfo.displaySymbol,
        value: this.contract.displayMultiplier,
      });
    }
    return "";
  }

  get powerMarkPriceTip() {
    if (this.symbolInfo && this.symbolInfo.displaySymbol && this.symbolInfo.symbol) {
      return Intl.eval("trade", "power-mark-price", {
        unit: this.symbolInfo.symbol,
        symbol: this.symbolInfo.displaySymbol,
        multiplier: this.displaySymbolMultiplier,
      });
    }
    return "";
  }

  get fundingCoefficientTip() {
    if (this.contract && this.contract.fundingRateCoefficient && this.symbolInfo) {
      const propName = this.symbolInfo.isPower
        ? Intl.get("trade", "theoretical-price")
        : Intl.get("trade", "index-price");
      return Intl.eval("trade", "funding-coefficient-hover", {
        unit: this.symbolInfo.unit,
        value: this.contract.fundingRateCoefficient,
        pricePropName: propName.toLowerCase(),
      });
    }
    return "";
  }

  get multiplierTip() {
    if (this.contract && this.pool) {
      return `${Intl.get("trade", "the-notional-value-of")} ${this.contract.minTradeUnit}${this.symbolInfo.unit
        }`;
    }
    return "";
  }

  get TransactionFeeTip() {
    if (this.contract && this.contract.feeRatioNotional && this.contract.feeRatioMark) {
      return `Min( ${this.contract.feeRatioNotional * 100}% * Notional, ${this.contract.feeRatioMark * 100
        }% * Mark Price )`;
    }
    return "";
  }

  /**
   * 最大输入数量
   * 分为两种情况
   * 1. closeOnly false
   *  max entry volume = dynamic effective margin / symbol's price (etc BTC)
   * 2. closeOnly true
   *  max entry volume = current symbol margin held / symbol's price (etc BTC)
   */
  get maxEntryVolume() {
    const maxVolume = this.calculateVolume(this.maxAvailableMargin);
    return this.hasSpecialFormat
      ? bg(maxVolume)
        .div(SHORT_NAME_MAP[this.symbolInfo.formattedUnit])
        .toFixed(this.minTradeScaleDecimal)
      : maxVolume;
  }

  //最小输入数量
  get minEntryVolume() {
    return this.contract && this.hasSpecialFormat
      ? bg(this.contract.multiplier).div(SHORT_NAME_MAP[this.symbolInfo.formattedUnit]).toNumber()
      : this.contract.minTradeUnit;
  }

  get curTradeUnit() {
    return this.symbolInfo && this.symbolInfo.displayUnit ? `${this.symbolInfo.displayUnit}` : "";
  }

  //有效数字
  get minTradeScaleDecimal() {
    return this.contract && precision(this.contract.minTradeUnit);
  }

  get calcMinTradeScaleDecimal() {
    return this.contract && precision(this.contract.multiplier);
  }

  get dollarValue() {
    if (this.entryStatus === TRADE_ENTRY_DOLLAR_STATUS) {
      return this.inputDollarValue;
    } else {
      const value = bg(this.displayVolume).times(this.takePrice).toFixed(2);
      return this.hasSpecialFormat
        ? bg(value).times(SHORT_NAME_MAP[this.symbolInfo.formattedUnit]).toFixed(2)
        : value;
    }
    // this.entryStatus !== TRADE_ENTRY_DOLLAR_STATUS && !this.inputDollarValue && this.realTradeVolume ? bg(this.realTradeVolume).times(this.takePrice).toFixed(2) : this.inputDollarValue
  }

  get maxDollarValue() {
    return bg(this.calculateVolume(this.maxAvailableMargin)).times(this.takePrice).toFixed(2);
  }

  get sliderValue() {
    if (this.entryStatus === TRADE_ENTRY_SLIDER_STATUS) {
      return this.inputSliderValue;
    } else {
      const value =
        this.displayVolume && this.takePrice && this.initialMarginRatio
          ? bg(this.displayVolume).times(this.initialMarginRatio).times(this.takePrice).toNumber()
          : 0;
      return this.hasSpecialFormat
        ? bg(value).times(SHORT_NAME_MAP[this.symbolInfo.formattedUnit]).toNumber()
        : value;
    }
  }

  get volumeValue() {
    const value =
      this.entryStatus !== TRADE_ENTRY_VOLUME_STATUS
        ? bg(this.volume).toFixed(this.minTradeScaleDecimal)
        : this.volume;
    return value;
  }

  //解析用户输入的volume，根据单位不一样，值不一样
  get displayVolume() {
    if (this.entryStatus !== TRADE_ENTRY_VOLUME_STATUS && this.hasSpecialFormat) {
      return bg(this.volume)
        .div(SHORT_NAME_MAP[this.symbolInfo.formattedUnit])
        .toFixed(this.minTradeScaleDecimal);
    } else {
      return this.volume;
    }
  }

  // get openInterst() {
  //   if (this.hasSpecialFormat && this.volume) {

  //   }
  //   return false
  // }

  get realTradeVolume() {
    if (this.volume) {
      let entryVolume = bg(this.volume)
        .times(SHORT_NAME_MAP[this.symbolInfo.formattedUnit])
        .toFixed(this.minTradeScaleDecimal)
      let addVolume = 0;
      if ((+this.position.volume < 0 && this.direction === SELL) || (+this.position.volume > 0 && this.direction === BUY) || +this.position.volume === 0) {
        addVolume = entryVolume
      } else if ((+this.position.volume < 0 && this.direction === BUY) || (+this.position.volume > 0 && this.direction === SELL)) {
        if (bg(Math.abs(this.position.volume)).gte(entryVolume)) {
          this.setOpenInterst(false)
        } else {
          addVolume = bg(Math.abs(this.position.volume)).minus(entryVolume).toNumber()
          addVolume = Math.abs(addVolume)
        }
      }
      let bool = calculateOpenInterest(addVolume, this)
      this.setOpenInterst(bool)
    }
    if (this.hasSpecialFormat && this.volume) {
      return bg(this.displayVolume)
        .times(SHORT_NAME_MAP[this.symbolInfo.formattedUnit])
        .toFixed(this.calcMinTradeScaleDecimal);
    } else {
      return this.volume;
    }
  }

  //symbol 是否需要另外的multiplier
  get hasFormatUnit() {
    return this.symbolInfo && this.symbolInfo.showUnitConversion;
  }

  get hasSpecialFormat() {
    return this.symbolInfo && this.symbolInfo.formattedUnit;
  }

  //开仓价格
  get takePrice() {
    return this.symbolInfo && this.position && (this.symbolInfo.isPower || this.symbolInfo.isGamma)
      ? bg(this.position.theoreticalPrice).toFixed(0)
      : this.position.price;
  }

  get formatSliderValue() {
    return this.sliderValue && bg(this.sliderValue).toFixed(this.minTradeScaleDecimal);
  }

  //只负责平仓交易状态下slider bar 的渐变，其他状态css控制
  get sliderStyle() {
    const buyColor = "#59AE99";
    const sellColor = "#CE5E5E";
    const railBuyColor = "rgba(89, 174, 153, 0.1)";
    const railsSellColor = "rgba(206, 94, 94, 0.1)";
    const defaultStyle = {
      trackStyle: {},
      railStyle: {},
      handleStyle: {},
    };
    let trackStartColor, trackEndColor;
    let railStartColor, railEndColor;
    let handleColor;
    let startPercent;
    // let margin, available;
    const sliderBarLength = 331;
    if (this.positionTradingStatus === TRADING_CLOSE_POSITION_STATUS) {
      if (!this.closeOnly) {
        //右边往左，右边是占用比例
        startPercent = bg(this.accountInfo.curSymbolMarginUsage).div(this.maxAvailableMargin);
        const start = bg(sliderBarLength).times(startPercent).toNumber();
        const gradient = start + 20;
        const end = sliderBarLength - start;
        if (this.isPositive) {
          trackStartColor = buyColor;
          trackEndColor = sellColor;
          railStartColor = railBuyColor;
          railEndColor = railsSellColor;
          handleColor = bg(this.sliderValue).gt(this.accountInfo.curSymbolMarginUsage)
            ? sellColor
            : buyColor;
        } else {
          trackStartColor = sellColor;
          trackEndColor = buyColor;
          railStartColor = railsSellColor;
          railEndColor = railBuyColor;
          handleColor = bg(this.sliderValue).gt(this.accountInfo.curSymbolMarginUsage)
            ? buyColor
            : sellColor;
        }
        return {
          trackStyle: {
            background: `linear-gradient(to right, ${trackStartColor} ${start}px, ${trackEndColor} ${gradient}px ,${trackEndColor} ${end}px`,
          },
          railStyle: {
            background: `linear-gradient(to right, ${railStartColor} ${start}px, ${railEndColor} ${gradient}px ,${railEndColor} ${end}px`,
          },
          handleStyle: {
            background: handleColor,
            borderColor: handleColor,
          },
        };
      } else {
        return defaultStyle;
      }
    } else {
      return defaultStyle;
    }
  }

  //正在交易状态，（开仓、加仓、平仓）
  get positionTradingStatus() {
    if (
      (this.isPositive && this.direction === BUY) ||
      (this.isNegative && this.direction === SELL)
    ) {
      return TRADING_APPEND_POSITION_STATUS;
    } else if (
      (this.isPositive && this.direction === SELL) ||
      (this.isNegative && this.direction === BUY)
    ) {
      return TRADING_CLOSE_POSITION_STATUS;
    } else {
      return TRADING_CREATE_POSITION_STATUS;
    }
  }

  //根据当前账户可用保证金，然后用二分算法算出最大可用保证金
  get maxAvailableMargin() {
    if (this.position && this.pool && this.initialMarginRatio && this.accountInfo) {
      const { availableMargin = 0, curSymbolMarginUsage = 0 } = this.accountInfo;
      let maxAvailableMargin = availableMargin;
      if (this.closeOnly) {
        return curSymbolMarginUsage;
      } else {
        //平仓
        if (this.positionTradingStatus === TRADING_CLOSE_POSITION_STATUS) {
          maxAvailableMargin = bg(availableMargin)
            .plus(curSymbolMarginUsage * 2)
            .toNumber();
          return bg(this.findAvaiableMargin(maxAvailableMargin, this.direction));
        } else {
          return this.findAvaiableMargin(maxAvailableMargin, this.direction);
        }
      }
    }
    return 0;
  }

  findAvaiableMargin(margin, direction) {
    const stopValue = bg(margin).times(0.01).toNumber();
    let volume = this.calculateVolume(margin);
    let counter = 0;
    while (++counter < 300 && margin > 0) {
      volume = direction === BUY ? volume : -volume;
      const unrealizedPnl = this.calculateUnrealizePnl(volume);
      const fee = this.calculateTransactionFee(volume);
      const marginHeld = bg(this.takePrice).times(volume).times(this.initialMarginRatio).abs();
      const marginKeep = bg(margin).plus(unrealizedPnl).minus(fee).minus(marginHeld).toNumber();
      const step = bg(volume).times(0.01);
      if (marginKeep >= this.contract.multiplier && marginKeep <= stopValue) {
        // volume = bg(volume).minus(step).abs().toNumber();
        break;
      } else if (marginKeep < 0) {
        volume = bg(volume).minus(step).abs().toNumber();
      } else {
        volume = bg(volume).plus(step).abs().toNumber();
      }
      // console.log('volume,unrealizedPnl,fee,marginKeep ',volume,unrealizedPnl,fee,marginKeep)
    }
    return bg(volume).times(this.takePrice).times(this.initialMarginRatio).abs().toNumber();
  }

  get initialMarginRatio() {
    return this.symbolInfo.isGamma
      ? (this.positions.length > 0 ? this.position.initialMarginPerVolume : this.initialMarginPerVolumeForGamma) / this.position.theoreticalPrice
      : this.symbolInfo.isPower
        ? (this.position && this.position.initialMarginPerVolume ? this.position.initialMarginPerVolume / this.position.theoreticalPrice : this.contract.initialMarginRatio)
        : this.contract && this.contract.initialMarginRatio;
  }

  get maintenanceMarginRatio() {
    return this.contract && this.contract.maintenanceMarginRatio;
  }

  get directionDisplay() {
    if (this.direction === BUY) {
      return "long";
    } else {
      return "short";
    }
  }

  //power's volume need to times multiplier
  getPowerVolume(volume) {
    if ((this.symbolInfo.isPower || this.symbolInfo.isGamma) && SHORT_NAME_MAP[this.symbolInfo.formattedUnit]) {
      return bg(volume)
        .div(SHORT_NAME_MAP[this.symbolInfo.formattedUnit])
        .toFixed(this.minTradeScaleDecimal);
    } else {
      return volume;
    }
  }

  get inputHelpText() {
    let openText =
      this.pool &&
      this.symbolInfo &&
      `${Intl.get("trade", "open-a")} ${bg(this.getPowerVolume(this.realTradeVolume)).toFixed(
        this.minTradeScaleDecimal
      )} ${this.symbolInfo.unit} ${this.directionDisplay} ${this.symbolInfo.optionType} ${Intl.get(
        "trade",
        "position"
      ).toLowerCase()}`;
    if (this.positionTradingStatus === TRADING_CLOSE_POSITION_STATUS) {
      let percentage = bg(this.sliderValue).div(this.maxAvailableMargin).times(100);
      percentage =
        percentage.gt(100) || +this.displayVolume + +this.position.volume === 0
          ? 100
          : percentage.toFixed(0, 0);
      const closeText = `${percentage}% ${Intl.get("trade", "close-position")}`;
      if (this.closeOnly) {
        return closeText;
      } else {
        let percentage = bg(this.sliderValue || 0)
          .div(bg(this.accountInfo.curSymbolNotional).toFixed(this.minTradeScaleDecimal))
          .times(100);
        const closeText = `${percentage.gt(100) ? 100 : percentage.toFixed(0)}% ${Intl.get(
          "trade",
          "close-position"
        )}`;
        const amount = this.direction === BUY ? this.realTradeVolume : -this.realTradeVolume;
        const volume = bg(this.getPowerVolume(bg(this.position.volume).plus(amount).abs()));
        if (percentage.gte(100) && volume.gte(this.contract.minTradeUnit)) {
          openText = `${Intl.get("trade", "open-a")} ${volume.toFixed(this.minTradeScaleDecimal)} ${this.symbolInfo.unit
            } ${this.directionDisplay} ${this.symbolInfo.optionType} ${Intl.get(
              "trade",
              "position"
            ).toLowerCase()}`;
          return `${closeText} + ${openText}`;
        } else {
          return closeText;
        }
      }
    } else {
      return openText;
    }
  }

  get tradeWarn() {
    return (
      this.isRetired ||
      (!this.closeOnly &&
        this.maxAvailableMargin &&
        !isNaN(this.realTradeVolume) &&
        this.realTradeVolume &&
        bg(this.realTradeVolume).eq(this.calculateVolume(this.maxAvailableMargin)))
    );
  }

  get isAccountReady() {
    return this.wallet && this.pool && this.pool.address;
  }

  get isTradingReady() {
    return this.maxAvailableMargin > 0 && this.displayVolume > 0;
  }

  get isRetired() {
    return (this.pool && this.pool.retired) || this.symbolInfo.retired;
  }

  get skipTradeConfirmation() {
    return sessionStorage.getItem(SKIP_TRADE_CONFIRMATION) === "1";
  }

  get fundingFeeTip() {
    if (type.isFuture) {
      if (Category.isPower && this.symbolInfo.underlying) {
        return Intl.eval("trade", "daily-funding-hover-power", {
          unit: this.symbolInfo.underlying,
        });
      } else {
        return Intl.get("trade", "daily-funding-hover-future");
      }
    } else {
      return Intl.get("trade", "daily-funding-hover-option");
    }
  }

  mergeMarkpriceChange(symbol, symbolsWithMarkprice) {
    const find = symbolsWithMarkprice.find(
      (item) => item.pool === symbol.address && item.symbolId === symbol.symbolId
    );
    if (find) {
      const { volume24h, markPrice, markPrice24h, indexPriceVolume24h } = find;
      symbol.volume24h = volume24h;
      symbol.markPrice = markPrice;
      symbol.markPrice24h = markPrice24h;
      symbol.indexPriceVolume24h = indexPriceVolume24h;
    }
    return symbol;
  }

  get symbolInfos() {
    let symbols = this.symbols;
    if (this.symbolsWithMarkprice && this.symbolsWithMarkprice.length) {
      return symbols.map((s) => this.mergeMarkpriceChange(s, this.symbolsWithMarkprice));
    } else {
      return symbols;
    }
  }

  get symbolInfo() {
    if (
      this.symbolsWithMarkprice &&
      this.symbolsWithMarkprice.length &&
      this.symbolsWithMarkprice.length > 0 &&
      this.currentSymbol
    ) {
      return this.mergeMarkpriceChange(this.currentSymbol, this.symbolsWithMarkprice);
    } else {
      return this.currentSymbol || {};
    }
  }

  get directionChanged() {
    return {
      className:
        +this.volumeChanged.nowValue > 0
          ? "to-long"
          : +this.volumeChanged.nowValue < 0
            ? "to-short"
            : "close",
      before: firstLetterUppercase(
        this.isPositive ? LONG : this.isNegative ? SHORT : Intl.get("trade", "none")
      ),
      now: firstLetterUppercase(
        +this.volumeChanged.nowValue > 0 ? LONG : +this.volumeChanged.nowValue < 0 ? SHORT : ""
      ),
    };
  }

  get volumeChanged() {
    const curVolume = this.direction === BUY ? this.realTradeVolume : -this.realTradeVolume;
    let nowVolume = bg(curVolume).plus(this.position.volume).toNumber();
    const beforeVolume = this.getPowerVolume(this.position.volume);
    return {
      before:
        +this.position.volume === 0
          ? ""
          : `${bg(beforeVolume).toFixed(this.minTradeScaleDecimal)} ${this.symbolInfo.unit}`,
      nowValue: this.getPowerVolume(nowVolume),
      now: `${this.getPowerVolume(nowVolume)} ${this.symbolInfo.unit}`,
      totalVolume: this.getPowerVolume(this.realTradeVolume),
      notional: bg(nowVolume).times(this.position.price).toNumber(),
    };
  }

  get tradeBToken() {
    return this.symbolInfo && this.symbolInfo.bTokenSymbol ? this.symbolInfo.bTokenSymbol : "BUSD";
  }

  get gammaEntryPricePlacehold() {
    const median = this.index && Math.round(bg(this.index).toFixed(0).length / 2);
    return this.index && median && Math.floor(this.index / 10 ** median) * 10 ** median;
  }

  get initialMarginPerVolumeForGamma() {
    if (this.pool && this.wallet && this.symbolInfo && (this.gammaEntryPrice || this.index)) {
      const args = [
        this.wallet.detail.chainId,
        this.pool.address,
        this.symbolInfo.symbol,
        1,
        this.gammaEntryPrice || -1,
        true
      ];
      return ApiProxy.syncRequest("getInitialMarginPerGammaVolume", args);
    }
    return null;
  }


}
