
export const isGammaSymbol = (symbol) => {
  return typeof symbol === "object" ? symbol.category === "gamma" : /gamma/i.test(symbol);
};

export const normalizeGammaSymbol = symbol => {
  return isGammaSymbol(symbol) && symbol.split('-')[0]
}

export function _calculateSlippageCost(indexPrice, futuresK, netFuturesVolume, tradeFuturesVolume) {
  return (
    (2 * netFuturesVolume + Number(tradeFuturesVolume)) * indexPrice * futuresK / 2 * tradeFuturesVolume
  );
}
// return (2 * netFuturesVolume + tradeFuturesVolume) * indexPrice / ONE * futuresK / ONE / 2 * tradeFuturesVolume / ONE

export function _calculateCost(indexPrice, K, tradersNetVolume, tradeVolume) {
  return (
    ((((Number(tradersNetVolume) + Number(tradeVolume)) ** 2 - Number(tradersNetVolume) ** 2) * K) / 2 + Number(tradeVolume)) *
    indexPrice
  );
}


export const calculateTraderFunding = (s, p) => {
  const v1 = Number(s.preIndexPrice) + Number(s.curIndexPrice);
  const v2 = (s.preIndexPrice ** 2 + s.curIndexPrice ** 2) / s.oneHT;
  const v3 = v1 * v2 / s.oneHT;
  const v4 = v2 + s.preIndexPrice * s.curIndexPrice / s.oneHT;

  let diffA =  v3 * s.netPowerVolume * s.futuresK + v4 * (2 * s.netRealFuturesVolume * s.futuresK + s.netPowerVolume * s.powerK + 1 - s.oneHT) / 3;    

  let diffB = ((v4 * s.netPowerVolume * 2) / 3 + (v1 * s.netRealFuturesVolume) / 2) * s.futuresK;

  const dt = Date.now() / 1000 - s.fundingTimestamp;

  diffA = (diffA * dt) / s.fundingPeriod;
  diffB = (diffB * dt) / s.fundingPeriod;

  const preNetEffectiveFuturesVolume = 2 * s.netPowerVolume * s.preIndexPrice / s.oneHT + Number(s.netRealFuturesVolume);
  const curNetEffectiveFuturesVolume = 2 * s.netPowerVolume * s.curIndexPrice / s.oneHT + Number(s.netRealFuturesVolume);

  if (Number(s.netPowerVolume) !== 0) {
    const slippageCost = _calculateSlippageCost(
      s.curIndexPrice,
      s.futuresK,
      preNetEffectiveFuturesVolume,
      Number(curNetEffectiveFuturesVolume) - Number(preNetEffectiveFuturesVolume)
    );
    diffA += slippageCost / s.netPowerVolume;
  }

  let cumulaitveFundingPerPowerVolume = Number(s.cumulaitveFundingPerPowerVolume) + diffA;
  let cumulativeFundingPerRealFuturesVolume = Number(s.cumulativeFundingPerRealFuturesVolume) + diffB;



  // Trader未结算funding
  const traderFunding =(cumulaitveFundingPerPowerVolume - Number(p.cumulaitveFundingPerPowerVolume)) * p.powerVolume +
    (cumulativeFundingPerRealFuturesVolume - Number(p.cumulativeFundingPerRealFuturesVolume)) * (p.realFuturesVolume);
  return traderFunding;
};

export const calculateFundingByPeriod = (s,dt = 3600 * 24) => {
  const v1 = Number(s.curIndexPrice) + Number(s.curIndexPrice);
  const v2 = (s.curIndexPrice ** 2 + s.curIndexPrice** 2) / s.oneHT;
  const v3 = (v1 * v2) / s.oneHT;
  const v4 = v2 + (s.curIndexPrice * s.curIndexPrice) / s.oneHT;

  let diffA =
    v3 * s.netPowerVolume * s.futuresK +
    (v4 * (2 * s.netRealFuturesVolume * s.futuresK + s.netPowerVolume * s.powerK + 1 - s.oneHT)) /
    3;
  let diffB = ((v4 * s.netPowerVolume * 2) / 3 + (v1 * s.netRealFuturesVolume) / 2) * s.futuresK;

  diffA = (diffA * dt) / s.fundingPeriod;
  diffB = (diffB * dt) / s.fundingPeriod;

  const realFuturesVolume = -2 * s.curIndexPrice  / s.oneHT
  const dailyFunding = (diffA * 1 + diffB * realFuturesVolume)

  return dailyFunding;  
};



export function calculateGammaUnrealizedPnl(s, p) {
  return calculateGammaDpmmCost(s, p, null, s.curIndexPrice, p.entryPrice) - Number(p.cost)
}

export function calculateGammaIndexPnl(s,p){
  const powerValue = p.volume * s.powerTheoreticalPrice
  const realFuturesValue = p.realFuturesVolume * s.curIndexPrice
  return Number(powerValue) + Number(realFuturesValue) - Number(p.cost)
}

export function calculateGammaDpmmCost(s, p, tradeVolume, curIndexPrice, entryPrice,isOpen) {
  const curNetEffectiveFuturesVolume =
    (2 * s.netPowerVolume * curIndexPrice) / s.oneHT + Number(s.netRealFuturesVolume);
  const realFuturesVolume = p ? Number(p.realFuturesVolume) : (-2 * entryPrice / s.oneHT) * tradeVolume
  const curPowerVolume = p ? Number(p.powerVolume) : tradeVolume
  const powerValue = isOpen ? 
    _calculateCost(
      s.powerTheoreticalPrice,
      s.powerK,
      s.netPowerVolume,
      curPowerVolume
    )
  : - _calculateCost(
    s.powerTheoreticalPrice,
    s.powerK,
    s.netPowerVolume,
    -curPowerVolume
  );
  // const powerValue = s.powerTheoreticalPrice * curPowerVolume
  const realFuturesValue = realFuturesVolume * curIndexPrice;
  const effectiveFuturesVolume =
    (2 * (tradeVolume || curPowerVolume) * curIndexPrice) / s.oneHT + Number(realFuturesVolume);
  const slippage = _calculateSlippageCost(
    curIndexPrice,
    s.futuresK,
    curNetEffectiveFuturesVolume,
    isOpen ? effectiveFuturesVolume :-effectiveFuturesVolume
  );
  const result = Number(powerValue) + Number(realFuturesValue) - Number(slippage)
  // console.log('curIndexPrice', curIndexPrice, 'powerValue ', powerValue, 'futures Value ', realFuturesValue, 'slippage ', slippage, 'result ', result)
  return result;
}



export function calculateGammaCost(s, tradeVolume, curIndexPrice, entryPrice) {
  const realFuturesVolume =  (-2 * entryPrice / s.oneHT) * tradeVolume
  const powerValue = s.powerTheoreticalPrice * tradeVolume
  const realFuturesValue = realFuturesVolume * curIndexPrice;
  const result = Number(powerValue) + Number(realFuturesValue) //- Number(slippage)
  return result;
}

export function estimateGammaInitialMargin(s,tradeVolume,tradeEntryPrice){    
  let finalPowerVolume, finalRealFuturesVolume;
  finalPowerVolume = tradeVolume;
  finalRealFuturesVolume = - (2 * tradeEntryPrice * tradeVolume) / s.oneHT;
  const averageEntryPrice = (-finalRealFuturesVolume * s.oneHT) / finalPowerVolume / 2;
  //entry price -1 means use curIndexPrice
  return (
    (Math.abs(tradeEntryPrice === -1 ? 0 : s.curIndexPrice - averageEntryPrice) * 2 + s.curIndexPrice * s.initialMarginRatio) *
    s.curIndexPrice *
    s.initialMarginRatio *
    Math.abs(finalPowerVolume)
  );
}

export function calculateGammaInitialMargin(s, p, tradeVolume, tradeEntryPrice) {
  const curPowerVolume = p ? Number(p.powerVolume) : tradeVolume;
  const realFuturesVolume = p ? Number(p.realFuturesVolume) : (-2 * tradeEntryPrice / s.oneHT) * tradeVolume;
  // 当用户trade输入 (tradePowerVolume, tradeEntryPrice) 的时候，可以通过上面两个量计算用户所需 initial margin
  let finalPowerVolume, finalRealFuturesVolume;
  if (tradeVolume * curPowerVolume >= 0) {
    // 开仓
    finalPowerVolume = Number(curPowerVolume) + Number(tradeVolume);
    finalRealFuturesVolume =
      Number(realFuturesVolume) - (2 * tradeEntryPrice * tradeVolume) / s.oneHT;
  } else {
    // 平仓
    finalPowerVolume = Number(curPowerVolume) + Number(tradeVolume);
    finalRealFuturesVolume =
      realFuturesVolume * (1 - Math.abs(tradeVolume) / Math.abs(p.powerVolume));
  }
  const averageEntryPrice = finalPowerVolume !== 0 ? (-finalRealFuturesVolume * s.oneHT) / finalPowerVolume / 2 : 0
  const result = (
    (Math.abs(s.curIndexPrice - averageEntryPrice) * 2 + s.curIndexPrice * s.initialMarginRatio) *
    s.curIndexPrice *
    s.initialMarginRatio *
    Math.abs(finalPowerVolume)
  )
  // console.log('calculateGammaInitialMarginPerVolume result ',result)
  return result;
}

export function calculateGammaAndDelta(s, entryPice, curIndexPrice, volume) {
  const gamma = (2 / s.oneHT) * Number(volume);
  const detla = ((2 / s.oneHT) * (Number(curIndexPrice) - Number(entryPice))) * Number(volume)
  return {
    gamma,
    detla
  }
}
