import Header from './components/Header/Header';
import Announcement from './components/Announcement/Announcement'
import TransactionState from './components/TransactionState/TransactionState'
import Intro from './components/Intro/Intro'
import PageRouter from './pages/PageRouter';
import { useLocation } from 'react-router-dom'
import './app.less';
import './assets/css/fonts.less'
import classNames from 'classnames';
import React, { useEffect } from 'react'
import useRpcProvider from './hooks/useRpcProvider';
import { buried, getCookie } from './utils/utils'
import Welcome from './components/Welcome/Welcome';
import { show } from 'react-functional-modal';
import GammaRiskWarning from './components/RiskWarning/GammaRiskWarning';
function App() {
  const location = useLocation();
  useRpcProvider()
  const curRouterClass = location.pathname.split('/')[1]
  const trade = location.pathname.split('/')[2]
  const rootClass = classNames('deri-root', curRouterClass, trade)
  useEffect(() => {
    if (window.gtag && curRouterClass) {
      let home = curRouterClass === "index" ? "home" : curRouterClass
      if (trade) {
        home = trade
      }
      buried("screen_view", "screen_name", home)
    }
  }, [location, window.gtag])

  useEffect(() => {
    let cookie = getCookie("gammaRiskWarning-modal")
    if (cookie !== "false" && trade === "gamma") {
      show(<GammaRiskWarning />, {
        key: "GammaRiskWarning",
        fading: true,
        style: {
          background: "rgba(0, 0, 0, 0.4)",
          zIndex: 9,
        },
      })
    }
   
  }, [])
  return (
    <div className={rootClass}>
      <Announcement />
      <Intro trade={trade} />
      {/* <Welcome path={curRouterClass}  /> */}
      <Header></Header>
      <PageRouter></PageRouter>
      <TransactionState />
    </div>
  );
}

export default App
