export const getWalletBalance = async (chainId, poolAddress, accountAddress, bTokenSymbol) => {
  return catchApiError(
    async () => {
      [chainId, poolAddress, accountAddress] = checkApiInput(chainId, poolAddress, accountAddress);
      bTokenSymbol = checkToken(bTokenSymbol);
      if (nativeCoinSymbols(chainId).includes(bTokenSymbol)) {
        const api = poolApiFactory(chainId, poolAddress);
        await api.pool._init();
        return fromWei(await api.pool.web3.eth.getBalance(accountAddress));
      } else {
        const api = poolApiFactory(chainId, poolAddress);
        await api.init();
        const bToken = ERC20Factory(chainId, getBTokenAddress(bTokenSymbol, api.pool.bTokens));
        return await bToken.balanceOf(accountAddress);
      }
    },
    [],
    ""
  );
};

export const isUnlocked = async (chainId, poolAddress, accountAddress, bTokenSymbol) => {
  return catchApiError(
    async () => {
      [chainId, poolAddress, accountAddress] = checkApiInput(chainId, poolAddress, accountAddress);
      const api = poolApiFactory(chainId, poolAddress);
      await api.init(ADDRESS_ZERO, true);
      bTokenSymbol = checkToken(bTokenSymbol);
      if (nativeCoinSymbols(chainId).includes(bTokenSymbol)) {
        return {
          isUnlocked: true,
          isZero: false,
          allowance: bg(MAX_UINT256).toString(),
        };
      } else {
        const baseToken = api.pool.bTokens.find((b) => b.bTokenSymbol === bTokenSymbol);
        if (baseToken) {
          const bToken = ERC20Factory(chainId, baseToken.bTokenAddress);
          const res = await bToken.isUnlocked(accountAddress, poolAddress);
          res.allowance = bg(res.allowance).div(10 ** baseToken.bTokenDecimals).toString()
          return res;
        } else {
          throw new Error(`cannot find bToken(${bTokenSymbol}) in pool(${poolAddress})`);
        }
      }
    },
    [],
    {
      isUnlocked: false,
      isZero: true,
      allowance: "0",
    }
  );
};

