export const calculateSimulateForGamma = (
  chainId,
  poolAddress,
  symbol,
  entryPrice,
  curIndexPrice,
  volume = 1,
  estimate,
  length = 100,
) => {
  return catchApiError(async () => {
    const api = poolApiFactory(chainId, poolAddress);
    await api.init();
    await api.pool.getSymbols();
    const indexPriceStart = "0";
    const indexPriceEnd = curIndexPrice * 2 < entryPrice ? entryPrice : curIndexPrice * 2;
    const indexPriceRange = bg(indexPriceEnd).minus(indexPriceStart).toString();
    let xAxis = Array(parseInt(length))
      .fill()
      .map((_, index) => index)
      .reduce(
        (acc, i) => {
          const prevousValue = acc.length === 0 ? 0 : acc[acc.length - 1];
          acc.push(bg(prevousValue).plus(bg(indexPriceRange).div(length)).toString());
          return acc;
        },
        ['0']
      )
    if(!xAxis.find(s => Number(s) === Number(entryPrice))) {
      xAxis.push(entryPrice)
      xAxis = xAxis.sort((s1,s2) => Number(s1) - Number(s2))
    }
    const data = xAxis.map((price) =>
      api.calculateSimulateForGamma(symbol, volume, entryPrice, curIndexPrice, price,estimate)
    ) ;
    // console.log('xAxis ',xAxis,'culculate result ',data,'symbol ' ,symbol,'entryPrice ',entryPrice,'openIndexPrice ',openIndexPrice)
    return data || [];
  },[],[]);
};

