import { useEffect, useRef, useState } from 'react'
import './header.less'
import logo from '../../assets/img/logo.svg'
import Menu from './Menu'
import { useRouteMatch, Link } from 'react-router-dom';
import LangSelector from './LangSelector'
import NetworkSelector from './NetworkSelector'
import WalletView from './WalletView'
import { inject, observer } from 'mobx-react'
import useLang from '../../hooks/useLang'
import Icon from '../Icon/Icon'
import classNames from 'classnames'
import { buried } from '../../utils/utils';


function Header({ intl, wallet }) {
  const [isShowWalletModal, setIsShowWalletModal] = useState(false)
  const lang = useLang(intl.dict, 'header');
  const isIndex = useRouteMatch('/index');
  const isRoot = useRouteMatch({ path: '/', exact: true })
  const isTeam = useRouteMatch('/team');
  const isTrade = useRouteMatch('/trade');
  const isDownload = useRouteMatch('/logo')
  const isInfo = useRouteMatch('/info')
  const [isShowSideMenu, setIsShowSideMenu] = useState(false)
  const showSideMenuClazz = classNames('header', { 'show-side-menu': isShowSideMenu, 'close-side-menu': !isShowSideMenu })
  const notConnectWalletPage = isIndex || isTeam || isRoot || isDownload || isInfo
  const linkRefOne = useRef(null);
  const linkRefTwo = useRef(null);
  const showSideMenu = () => {
    setIsShowSideMenu(true)
  }
  const showWalletModal = () => {
    setIsShowWalletModal(true)
  }
  const onGoLite = () => {
    buried("gsw_lite", "gsw_lite", "go_lite_web")
  }
  function handleMouseDownOne(event) {
    if (event.button === 1) {
      event.preventDefault();
      buried("gsw_lite", "gsw_lite", "go_lite_web")
      window.open(linkRefOne.current.href, '_blank');
    }
  }
  function handleMouseDownTwo(event) {
    if (event.button === 1) {
      event.preventDefault();
      buried("gsw_lite", "gsw_lite", "go_lite_web")
      window.open(linkRefTwo.current.href, '_blank');
    }
  }
  useEffect(() => {
    const linkOne = linkRefOne.current;
    const linkTwo = linkRefTwo.current;
    if (linkOne) {
      linkOne.addEventListener('mousedown', handleMouseDownOne);
    }
    if (linkTwo) {
      linkTwo.addEventListener('mousedown', handleMouseDownTwo);
    }

    return () => {
      if (linkOne) {
        linkOne.removeEventListener('mousedown', handleMouseDownOne);
      }
      if (linkTwo) {
        linkTwo.removeEventListener('mousedown', handleMouseDownTwo);
      }
    };
  }, []);

  return (
    <div className={showSideMenuClazz}>
      <div className='left'>
        <div className='small-menu' onClick={showSideMenu}><Icon token='menu' /></div>
        <div className='logo'>
          <Link to='/index'>
            <img src={logo} alt='logo' />
            <span className='name' >DERI</span>
          </Link>
        </div>
        {wallet.detail.chainId && +wallet.detail.chainId === 324 ? null : isTrade ? <div className="deri-lite-pro-tab">
          <div className="deri-pro">
            <div className="deri-pro-text">
              Pro
            </div>
            <a onClick={() => onGoLite()} href='https://lite.deri.io/#/lite/trade/futures' ref={linkRefOne}>
              <div className="tab-box">
                <Icon token="tab" />
              </div>
            </a>
          </div>
          <a onClick={() => onGoLite()} href='https://lite.deri.io/#/lite/trade/futures' ref={linkRefTwo}>
            <div className="deri-lite">
              <Icon token="m-tab" className="m-tab" />
              <div className="deri-lite-text">
                Lite
              </div>
            </div>
          </a>
        </div> : null}
        <Menu lang={lang} locale={intl.locale} closeSideMenu={() => setIsShowSideMenu(false)} />
      </div>
      <div className='right'>
        <Link className='work-href' target="_blank" to='/support'>
          <Icon token="work-order" />
        </Link>
        {!isInfo && <LangSelector />}
        {!notConnectWalletPage && <>
          <NetworkSelector showWalletModal={showWalletModal} />
          <WalletView lang={lang} isShowWalletModal={isShowWalletModal} />
        </>}
      </div>
    </div>
  )
}
export default inject('intl', "wallet")(observer(Header))