import { hide } from 'react-functional-modal'
import hint from '../Announcement/img/hint.svg'
import './riskWarning.less'
import { setCookie } from '../../utils/utils'
export default function GammaRiskWarning() {
  const closeRisk = () => {
    hide("GammaRiskWarning")
    setCookie("gammaRiskWarning-modal", "false")
  }
  return (
    <div className="risk-warning">
      <div className="risk-header">
        <div className='risk-img'>
          <img src={hint} alt='' />
          Risks related to Trading Gamma Swap
        </div>
      </div>
      <div className="risk-box">
        In its BETA stage, Gamma Swap may present unforeseen issues. As a novel derivative, it carries high risks and rewards. Before engaging, understand its unique features and associated risks. Trading may result in a total loss of invested funds. This disclaimer cannot cover all risks, so exercise caution and assume responsibility. Deri Protocol developers are not liable for any losses incurred from using Gamma Swap.
      </div>
      <div className="risk-close">
        <div className="risk-close-btn" onClick={closeRisk}>
          OK,  I Understand
        </div>
      </div>
    </div>
  )
}